import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sorting'
})
export class SortingPipe implements PipeTransform {

  transform(array: Array<any>, prop: any, args?: any): Array<string> {
    // console.log(prop);
    array.sort((a: any, b: any) => {
      if (a[prop] < b[prop]) {
        return -1 * args;
      } else if (a[prop] > b[prop]) {
        return 1 * args;
      } else if (a[prop] === undefined) {
        return -1 * args;
      } else {
        return 0;
      }
    });
    return array;
  }
}
